window.dom = {
    create(string) {
        let container = document.createElement('template'); //å› ä¸ºtemplateå¯ä»¥å®¹çº³æ‰€æœ‰å…ƒç´ ï¼Œä¾‹å¦‚divå°±ä¸èƒ½å®¹çº³tdæ ‡ç­¾
        container.innerHTML = string.trim();
        return container.content.firstChild;
    },
    before(node1, node2) {
        return node1.parentNode.insertBefore(node2, node1);
    },
    after(node1, node2) {
        return node1.parentNode.insertBefore(node2, node1.sibling); //æ²¡æœ‰ç›´æŽ¥å¯ä»¥åœ¨åŽé¢æ’å…ƒç´ çš„æ–¹æ³•
    },
    append(parent, child) {
        return parent.appendChild(child)
    },
    wrap(node, parent) {
        dom.before(node, parent); //å…ˆå°†è¦æ·»åŠ çš„å…ƒç´ æ”¾åœ¨è¢«æ·»åŠ å…ƒç´ ä¹‹å‰ï¼Œç›´æŽ¥æ·»åŠ ä¼šå°†çŽ°æœ‰å…ƒç´ åœ¨é¡µé¢ä¸Šçš„ä½ç½®ç§»åŽ»
        dom.append(parent, node);
    },
    remove(node) {
        node.parentNode.removeChild(node)
        return node;
    },
    empty(node) { //ä¹Ÿå¯ä»¥ç›´æŽ¥å°†innerHtmlè®¾ç½®ä¸ºç©ºï¼Œä½†ä¸å¥½å°†ç§»åŽ»çš„å†…å®¹è¿”å›ž
        let x = node.firstChild;
        let arr = [];
        while (x) {
            arr.push(dom.remove(x));
            x = node.firstChild;
        }
        return arr;
    },
    attr(node, name, value) {
        if (arguments.length === 2) {
            return node.getAttribute(name);
        } else if (arguments.length === 3) {
            node.setAttribute(name, value);
        }
    },
    text(node, string) { //å¦‚æžœåªæœ‰ä¸€ä¸ªå‚æ•°å°±è¯»å–å†…å®¹ï¼Œä¸¤ä¸ªå‚æ•°å°±ä¿®æ”¹å†…å®¹ï¼Œå‡½æ•°é‡è½½
        if (arguments.length === 1) {
            if ('innerText' in node) { //é˜²æ­¢ieæµè§ˆå™¨æ²¡æœ‰innerTextå±žæ€§
                return node.innerText;
            } else {
                return node.textContent;
            }
        } else if (arguments.length === 2) {
            if ('innerText' in node) {
                node.innerText = string;
            } else {
                node.textContent = string;
            }
        }
    },
    html(node, string) {
        if (arguments.length === 1) {
            return node.innerHTML;
        } else if (arguments.length === 2) {
            node.innerHTML = string;
        }
    },
    style(node, name, value) {
        if (arguments.length === 3) { //(node,'width','40px')å½¢å¼çš„ä¿®æ”¹
            node.style[name] = value;
        } else if (arguments.length === 2) {
            if (typeof name === 'string') {
                return node.style[name]; //è¯»å–æŸä¸ªèŠ‚ç‚¹æŸä¸ªæ ·å¼çš„å±žæ€§å€¼
            } else if (name instanceof Object) { //(node,'width:300px,height:100px')å½¢å¼çš„ä¿®æ”¹
                for (let key in name) { //è¦åœ¨keyå‰é¢åŠ ä¸Šlet
                    node.style[key] = name[key];
                }
            }
        }
    },
    class: {
        add(node, name) {
            node.classList.add(name);
        },
        remove(node, name) {
            node.classList.remove(name);
        },
        has(node, name) {
            return node.classList.contains(name);
        }
    },
    on(node, event, f) {
        node.addEventListener(event, f)
    },
    off(node, event, f) {
        node.removeEventListener(event, f)
    },
    find(selector, scope) {
        return scope || document.querySelectorAll(selector); //scopeçš„ç›®çš„æ˜¯æ‰¾ç‰¹å®šèŒƒå›´ä¸‹çš„å…ƒç´ 
    },
    parent(node) {
        return node.parentNode;
    },
    children(node) {
        return node.children;
    },
    siblings(node) {
        return Array.from(node.parentNode.children).filter(value => value !== node);
    },
    next(node) {
        return node.nextElementSibling;
    },
    previous(node) {
        let x = node.previousSibling; //æ²¡æœ‰previousElementSiblingå±žæ€§
        while (x && x.nodeType === 3) {
            x = x.previousSibling;
        }
        return x;
    },
    each(nodes, f) {
        for (let i = 0; i < nodes.length; i++) {
            f.call(null, nodes[i]);
        }
    },
    index(node) {
        let i = 0;
        for (; i < node.parentNode.children.length; i++) {
            if (node.parentNode.children[i] === node) {
                break;
            }
        }
        return i;
    }
}